import { useForm } from "react-hook-form";
import ExaTitle from "../../component/ExaTitle";
import {
  DFlexFixAuto,
  JoinContainer,
  Btn,
  ErrorText,
  Label,
  ProfilBox,
  InputBox,
  PasswordInput,
  InputChk2,
  BtnNextBox,
  container,
  item,
} from "../../css/joinStyle";
import { useEffect, useState } from "react";
import { IFormList } from "../../interface/join";
import { motion } from "framer-motion";
import { useNavigate } from "react-router-dom";
import { Helmet } from "react-helmet";

function JoinStep2() {
  const errorNick = [
    { id: "1", nick: "tomato" },
    { id: "2", nick: "orange" },
    { id: "3", nick: "aa" },
    { id: "4", nick: "ss" },
  ];
  const {
    register,
    handleSubmit,
    watch,
    setError,
    reset,
    setValue,
    formState: { errors },
  } = useForm<IFormList>({
    defaultValues: {
      radio: "yes",
    },
  });
  const navigate = useNavigate();
  const [passWordView, setPassWordView] = useState(true);
  const [passWordView2, setPassWordView2] = useState(true);
  const [localImg, setLocalImg] = useState("");
  const [profill, setProfill] = useState("");
  const HandlerPassWord = () => {
    setPassWordView((view) => !view);
  };
  const HandlerPassWord2 = () => {
    setPassWordView2((view) => !view);
  };

  const formSubmit = (data: IFormList) => {
    navigate("/portfolio/joinconfirm");
    localStorage.setItem("", JSON.stringify(data));
    localStorage.setItem("", JSON.stringify(profill));
  };

  const avatar = watch("image");
  useEffect(() => {
    // 
    if (avatar && avatar.length > 0) {
      const file = avatar[0];
      setProfill(URL.createObjectURL(Object(file)));
    } else {
      setProfill(
        "https://t4.ftcdn.net/jpg/01/64/87/01/360_F_164870164_IRVXO976UO5nd79niegmWB3AJMqwfs6n.jpg"
      );
    }
  }, [avatar]);

  useEffect(() => {
    //      input   .
    const storedData: IFormList = JSON.parse(
      localStorage.getItem("") || "{}"
    );
    const storedImage = JSON.parse(localStorage.getItem("") || "{}");
    setLocalImg(storedImage);
    if (Object.keys(storedData).length !== 0) {
      setValue("id", storedData.id);
      setValue("image", storedImage.image);
      setValue("nick", storedData.nick);
      setValue("pw", storedData.pw);
      setValue("pw2", storedData.pw2);
      setValue("email", storedData.email);
      setValue("radio", storedData.radio);
    }
  }, [setValue]);

  return (
    <JoinContainer>
      <Helmet>
        <title> |  </title>
      </Helmet>
      <ExaTitle text={`\n`} size="30px" />
      <form onSubmit={handleSubmit(formSubmit)}>
        <motion.ul variants={container} initial="hidden" animate="visible">
          <DFlexFixAuto as="li">
            <InputBox className="box__fixed">
              <ProfilBox>
                <input
                  className="input"
                  {...register("image", {
                    required: " ",
                  })}
                  id="picture"
                  type="file"
                  accept="image/*"
                  hidden
                />
                <label
                  className="img__view"
                  htmlFor="picture"
                  style={{
                    backgroundImage: `url(${
                      localImg.length !== undefined ? localImg : profill
                    })`,
                  }}
                >
                  <span className="img__view-text">  +</span>
                </label>
                <ErrorText>{errors.image?.message}</ErrorText>
              </ProfilBox>
            </InputBox>
            <div className="box__auto">
              <InputBox>
                <Label htmlFor="id"></Label>
                <input
                  className="input"
                  {...register("id", {
                    required: "  ",
                    minLength: {
                      value: 2,
                      message: "1  .",
                    },
                    onChange: (e) => {
                      watch("id");
                    },
                  })}
                  type="text"
                  id="id"
                  placeholder=" "
                  autoComplete="username"
                />
                <ErrorText>{errors.id?.message}</ErrorText>
              </InputBox>
              <InputBox>
                <Label htmlFor="id"></Label>
                <input
                  className="input"
                  {...register("nick", {
                    required: " ",
                    minLength: {
                      value: 2,
                      message: "1  .",
                    },
                    validate: (v) => {
                      const aa = errorNick.filter((data) => data.nick === v);
                      return v.includes(aa[0]?.nick)
                        ? `${aa[0]?.nick}  .`
                        : true;
                    },
                  })}
                  type="text"
                  id="nick"
                  placeholder="  .(tomato,orange no)"
                  autoComplete="username"
                />
                <ErrorText>{errors.nick?.message}</ErrorText>
              </InputBox>
            </div>
          </DFlexFixAuto>
          <motion.li variants={item}>
            <InputBox>
              <Label htmlFor="pw"></Label>
              <PasswordInput>
                <input
                  className="input"
                  {...register("pw", {
                    required: " ",
                    minLength: {
                      value: 2,
                      message: "1  .",
                    },
                  })}
                  type={passWordView ? "password" : "text"}
                  id="pw"
                  placeholder=" ."
                  autoComplete="new-password"
                />
                <button type="button" onClick={HandlerPassWord}>
                  {passWordView ? "" : ""}
                </button>
              </PasswordInput>
              <ErrorText>{errors.pw?.message}</ErrorText>
            </InputBox>
          </motion.li>
          <motion.li variants={item}>
            <InputBox>
              <Label htmlFor="pw2"> </Label>
              <PasswordInput>
                <input
                  className="input"
                  {...register("pw2", {
                    required: "  .",
                    minLength: {
                      value: 2,
                      message: "1  .",
                    },
                    onChange: (e) => {
                      const pw = watch("pw");
                      const pw2 = e.target.value;
                      if (pw !== pw2 && pw2.length > pw.length) {
                        setError("pw2", {
                          message: " .   ",
                        });
                      } else {
                        setError("pw2", {
                          message: "",
                        });
                      }
                    },
                  })}
                  type={passWordView2 ? "password" : "text"}
                  id="pw2"
                  placeholder="  ."
                  autoComplete="new-password"
                />
                <button type="button" onClick={HandlerPassWord2}>
                  {passWordView2 ? "" : ""}
                </button>
              </PasswordInput>
              <ErrorText>{errors.pw2?.message}</ErrorText>
            </InputBox>
          </motion.li>
          <motion.li variants={item}>
            <InputBox>
              <Label htmlFor="email"></Label>
              <input
                className="input"
                {...register("email", {
                  required: "email  ",
                  minLength: {
                    value: 2,
                    message: "1  .",
                  },
                  pattern: {
                    value: /^[a-zA-Z0-9._-]+@gmail.com$/,
                    message: "gmail ",
                  },
                })}
                type="email"
                id="email"
                placeholder="gmail  ."
              />
              <ErrorText>{errors.email?.message}</ErrorText>
            </InputBox>
          </motion.li>
          <motion.li variants={item}>
            <InputBox>
              <Label htmlFor="email" className="input__title">
                
              </Label>
              <InputChk2>
                <input
                  className="input"
                  {...register("radio", {
                    minLength: {
                      value: 2,
                      message: "1  .",
                    },
                  })}
                  type="radio"
                  id="rd1"
                  value="yes"
                />
                <Label htmlFor="rd1"></Label>
                <input
                  className="input"
                  {...register("radio", {
                    minLength: {
                      value: 2,
                      message: "1  .",
                    },
                  })}
                  type="radio"
                  id="rd2"
                  value="no"
                />
                <Label htmlFor="rd2"></Label>
              </InputChk2>
            </InputBox>
          </motion.li>
        </motion.ul>
        <BtnNextBox style={{ gap: "10px" }}>
          <Btn type="button" onClick={() => reset()}>
            
          </Btn>
          <Btn></Btn>
        </BtnNextBox>
      </form>
    </JoinContainer>
  );
}

export default JoinStep2;
